  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@keyframes slide-to-bottom {
  0% {
    opacity: 0;
    transform: translateY(-80px);
  }

  50% {
    opacity: .6;
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slide-to-right {
  0% {
    opacity: 0;
    transform: translateX(-150px);
  }

  60% {
    opacity: 1;
    letter-spacing: .3em;
    transform: translateX(0);
  }

  100% {
    letter-spacing: .05em;
    transform: translateX(0);
  }
}

@keyframes slide-to-top {
  0% {
    opacity: 0;
    transform: translateY(80px);
